.menu {
  position: relative;
}

#menu__toggle {
  display: none;
}

.menu__btn {
  display: none;
}

@media (max-width: 1200px) {
  .menu__btn {
    display: flex;
    position: absolute;
    top: 0px;
    right: 0px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }

  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $accent;
  }
  .menu__btn > span {
    background-color: $primery;
  }
  .menu__btn > span::before {
    content: "";
    top: -8px;
  }

  .menu__btn > span::after {
    content: "";
    top: 8px;
  }

  .menu__list {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 120px 0;
    list-style: none;
    text-align: center;
    background-color: #eceff1;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
  }

  .menu__list-item {
    display: block;
    padding: 12px 24px;
    color: $text;
    font-family: $userfont;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }

  .menu__list-item:hover {
    background-color: #cfd8dc;
  }

  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
  }

  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }

  #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }

  #menu__toggle:checked ~ .menu__list {
    visibility: visible;
    right: 0;
  }

  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    transition: $transition;
  }

  .menu__list {
    transition: $transition;
  }

  .menu__list-item {
    transition: $transition;
  }
}
