.header {
  background-image: url("../images/bac_header.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 120px;
  padding-top: 28px;
  & .menu {
    margin: 0px 10px;
  }
  & .social {
    margin-right: 10px;
  }
  &__inner {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__number {
    font-family: "nova_bold";
    font-weight: 700;
    font-size: 22px;
    line-height: 130%;
    color: #044d29;
    p {
      font-size: 13px;
      line-height: 130%;
      color: #787878;
      position: relative;
      padding-left: 23px;
      &::before {
        content: "";
        position: absolute;
        top: 4px;
        left: 0;
        right: 0;
        bottom: 0;
        width: 7px;
        height: 7px;
        background-color: $accent;
        border-radius: 50%;
      }
    }
  }
}
.logo {
  display: flex;
  align-items: center;
  &__img {
  }
}
.menu {
  &__list {
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 85px;
  }
  &__list-item {
  }
  &__list-link {
  }
}
.social {
  display: flex;
  align-items: center;
  justify-content: center;
  &__list-item {
    background: linear-gradient(180deg, #5ce6a2 0%, rgba(4, 77, 41, 0.66) 100%);
    border: 2px solid #ffffff;
    height: 40px;
    width: 40px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__img {
    padding: 10px;
  }
}
