.video {
  background-image: url("../images/bac_video.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  &__inner {
    padding-bottom: 175px;
    padding-top: 120px;
    max-height: 885px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  &__content {
    max-height: 470px;
    max-width: 1020px;
    position: relative;
    &::after {
      position: absolute;
      content: "";
      background-image: url("../images/video_logo.png");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 118px;
      height: 118px;
    }
    & img {
      width: 100%;
      height: 100%;
    }
  }
  &__title {
    padding: 10px 10px 70px;
  }

  &__watch {
  }
}
