.footer {
  background: #f1f1f1;
  max-height: 500px;
  padding: 35px 0 35px;
  &__inner {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__number {
    font-family: "nova_bold";
    font-weight: 700;
    font-size: 22px;
    line-height: 130%;
    color: #044d29;
    p {
      font-size: 13px;
      line-height: 130%;
      color: #787878;
      position: relative;
      padding-left: 23px;
      &::before {
        content: "";
        position: absolute;
        top: 4px;
        left: 0;
        right: 0;
        bottom: 0;
        width: 7px;
        height: 7px;
        background-color: $accent;
        border-radius: 50%;
      }
    }
  }
}
