.type {
  transform: translateY(-100px);
  &__title {
  }
  &__inner {
    margin-top: 35px;
    display: flex;
    justify-content: space-between;
    column-gap: 7px;
  }
  &__item {
    position: relative;
    border-radius: 5px;
    &::after {
      content: "";
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
      background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
      border-radius: 5px;
    }
  }
  &__item-img {
    & img {
      width: 100%;
    }
  }
  &__item-subtitle {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    width: 100%;
    text-align: center;
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 30px;
    line-height: 141.5%;
    color: #ffffff;
    z-index: 2;
  }
}
