// === DESKTOP FIRST ===

// xl <= Extra extra large (xxl)
@media (max-width: 1620px) {
  body {
    font-size: 20px;
  }
  .menu__list {
    column-gap: 50px;
  }
  .departure__form-content {
    display: flex;
    flex-direction: column;
    row-gap: 21px;
  }
  .departure__form-content input + input {
    margin-left: 0;
  }
  .departure__form p {
    padding: 30px 0 97px;
  }
  .services,
  .call {
    margin-bottom: 110px;
  }
}
@media (max-width: 1400px) {
  .social {
    display: none;
  }
  .greet {
    padding-top: 50px;
    padding-bottom: 70px;
    &__inner {
      column-gap: 60px;
    }
    &__content-text p {
      padding-bottom: 20px;
    }
  }
}

// lg <= Extra large (xl)
@media (max-width: 1200px) {
  .header__number {
    z-index: 2;
  }
  .logo {
    z-index: 2;
  }
  .menu {
    order: 2;
  }
  .social {
    display: flex;
    z-index: 2;
  }
  .hero__title {
    font-size: 58px;
  }
  .quality {
    &__img {
      width: 100px;
      height: 100px;
    }
  }
  .greet__content-text::after {
    height: 300px;
  }
  .title {
    font-size: 42px;
  }
  .departure__form p {
    font-size: 22px;
    text-align: center;
    padding: 30px 0 555px;
  }
  .departure__title {
    text-align: center;
  }
  .departure .container-big::after {
    right: auto;
    left: auto;
    bottom: 115px;
  }
  .departure__form-content {
    flex-direction: row;
    justify-content: space-around;
    gap: 20px;
  }
  .departure__form-btn {
    display: flex;
    justify-content: center;
    margin: 0 auto;
  }
  .type__item-subtitle,
  .services__item-subtitle {
    font-size: 26px;
  }
  .slider {
    margin-top: 0;
  }
  .footer {
    & .menu {
      display: none;
    }
  }
}

// md <= Large (lg)
@media (max-width: 992px) {
  .quality {
    display: none;
  }
  .greet {
    padding-top: 15px;
    padding-bottom: 45px;
    &__inner {
      column-gap: 0;
    }
    &__img {
      &::after {
        bottom: 53px;
        width: 300px;
        height: 475px;
      }
      img {
        width: 470px;
        height: 700px;
      }
    }
  }
  .video__inner {
    padding-top: 50px;
  }
  .departure .container-big::after {
    right: 100px;
  }
  .type__inner,
  .services__inner {
    flex-direction: column;
    row-gap: 12px;
    margin-bottom: 12px;
  }
}

// sm <= Medium (md)
@media (max-width: 768px) {
  .header {
    .social {
      display: none;
    }
  }
  .hero__title {
    font-size: 48px;
  }
  .greet__inner {
    flex-direction: column;
  }
  .quality {
    display: flex;
  }
  .greet__content-text::after {
    height: 190px;
  }
  .greet__content {
    padding-top: 0px;
  }
  .quality {
    padding-top: 25px;
  }
  .services {
    margin-bottom: 150px;
  }
}

// xs <= Small (sm)
@media (max-width: 576px) {
  body {
    font-size: 17px;
  }
  .header__number {
    display: none;
  }
  .hero {
    padding-top: 70px;
    text-align: center;
    position: relative;
    z-index: -1;
    &__btn {
      position: absolute;
      bottom: 0;
      left: auto;
      right: auto;
      transform: translateY(50%);
    }
  }
  .hero__inner {
    align-items: center;
  }
  .hero__title {
    font-size: 30px;
  }
  .quality {
    flex-direction: column;
    row-gap: 45px;
  }
  .quality__img {
    width: 150px;
    height: 150px;
  }
  .departure {
    position: relative;
    padding-bottom: 0;
    padding-top: 50px;
  }

  .departure .container-big::after {
    right: 0;
    width: 750px;
    bottom: 80px;
  }
  .departure__form-content {
    flex-direction: column;
    align-items: center;
  }
  .departure__form p {
    padding: 30px 0 455px;
  }
  .departure__form-btn,
  .calc__form-btn,
  .call__form-btn {
    position: absolute;
    bottom: 0;
    left: 50%;
    right: auto;
    transform: translate(-50%, 50%);
  }
  .title {
    font-size: 30px;
  }
  .steps {
    padding-top: 140px;
    padding-bottom: 140px;
  }
  .steps__list-num {
    left: 7px;
  }
  .steps__list-item {
    margin-left: 0;
    padding-left: 112px;
  }
  .calc,
  .call {
    padding-bottom: 70px;
    position: relative;
  }
  .producers {
    padding-top: 160px;
    padding-bottom: 83px;
  }
  .type {
    transform: none;
    padding-top: 50px;
  }
  .type__inner,
  .services__inner {
    margin-top: 16px;
  }
  .type__item-subtitle,
  .services__item-subtitle {
    font-size: 17px;
  }
  .slider {
    margin-top: 100px;
  }
  .services__item {
    height: 200px;
  }
  .call__form-content input {
    width: 350px;
  }
  .services {
    margin-bottom: 100px;
  }
  .call__subtitle {
    font-size: 17px;
  }
  .call {
    margin-bottom: 100px;
  }
  .footer {
    & .footer__number {
      display: none;
    }
  }
}
