// GENERAL CSS SETTINGS

body {
  font-family: $userfont;
  font-size: $regular-text;
  line-height: $lineheight;
  color: $text;
  min-width: 320px;
  overflow-x: hidden;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main {
  flex: 1 1 auto;
}
.container-big {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 10px;
  height: 100%;
}
.container {
  max-width: $containerWidth;
  margin: 0px auto;
  padding: 0px $containerPadding;
  height: 100%;
}

.btn {
  background: linear-gradient(
    260.7deg,
    #58d095 2.73%,
    #58d095 2.74%,
    #169456 111.9%
  );
  border-radius: 55px;
  padding: 20px 35px;
  color: #fff;
  min-width: 295px;
}

.title {
  font-family: "nova_extrabld";
  font-weight: 800;
  font-size: 50px;
  line-height: 130%;
  text-transform: uppercase;
  color: #232323;
  text-align: center;
}
