.greet {
  background-image: url("../images/bac_greet.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 80px;
  padding-bottom: 100px;
  &__inner {
    display: flex;
    column-gap: 135px;
  }
  &__img {
    position: relative;
    z-index: 1;
    &::after {
      content: "";
      position: absolute;
      width: 381px;
      height: 562px;
      left: 0;
      bottom: -61px;
      background: #044d29;
      z-index: -1;
    }
  }
  &__content {
    max-width: 920px;
    padding-top: 100px;
    margin-left: 30px;
    margin-right: 30px;
  }
  &__content-title {
    text-align: left;
  }
  &__content-text {
    margin-top: 30px;
    position: relative;
    &::after {
      content: "";
      background-color: rgba(88, 208, 149, 1);
      position: absolute;
      width: 2px;
      height: 230px;
      top: 25px;
      left: -25px;
    }
    & p {
      padding-bottom: 35px;
    }
  }
  &__img {
    width: 470px;
    height: 700px;
  }
  &__content-slogan {
    font-family: "nova_bold";
    font-weight: 700;
    font-size: 21px;
    line-height: 140%;
    color: #044d29;
  }
}
.quality {
  padding-top: 50px;
  display: flex;
  justify-content: space-between;
  // column-gap: 125px;
  align-items: center;

  &__item {
    text-align: center;
    min-height: 135px;
  }
  &__img {
    width: 118px;
    height: 118px;
    padding-bottom: 6px;
  }
  &__text {
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 21px;
    line-height: 140%;
    color: #044d29;
  }
}
