body {
  font-family: nova_regular, sans-serif;
  font-size: 23px;
  line-height: 1.4;
  color: rgba(0, 0, 0, 0.6);
  min-width: 320px;
  overflow-x: hidden;
}

.wrapper {
  overflow: hidden;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.main {
  flex: 1 1 auto;
}

.container-big {
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 10px;
  height: 100%;
}

.container {
  max-width: 1620px;
  margin: 0px auto;
  padding: 0px 10px;
  height: 100%;
}

.btn {
  background: linear-gradient(260.7deg, #58d095 2.73%, #58d095 2.74%, #169456 111.9%);
  border-radius: 55px;
  padding: 20px 35px;
  color: #fff;
  min-width: 295px;
}

.title {
  font-family: "nova_extrabld";
  font-weight: 800;
  font-size: 50px;
  line-height: 130%;
  text-transform: uppercase;
  color: #232323;
  text-align: center;
}

.header {
  background-image: url("../images/bac_header.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  min-height: 120px;
  padding-top: 28px;
}
.header .menu {
  margin: 0px 10px;
}
.header .social {
  margin-right: 10px;
}
.header__inner {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__number {
  font-family: "nova_bold";
  font-weight: 700;
  font-size: 22px;
  line-height: 130%;
  color: #044d29;
}
.header__number p {
  font-size: 13px;
  line-height: 130%;
  color: #787878;
  position: relative;
  padding-left: 23px;
}
.header__number p::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 7px;
  height: 7px;
  background-color: #044d29;
  border-radius: 50%;
}

.logo {
  display: flex;
  align-items: center;
}
.menu__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 85px;
}
.social {
  display: flex;
  align-items: center;
  justify-content: center;
}
.social__list-item {
  background: linear-gradient(180deg, #5ce6a2 0%, rgba(4, 77, 41, 0.66) 100%);
  border: 2px solid #ffffff;
  height: 40px;
  width: 40px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.social__link {
  display: flex;
  align-items: center;
  justify-content: center;
}
.social__img {
  padding: 10px;
}

.hero {
  height: calc(100vh - 120px);
  background-image: url("../images/bac-hero.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-bottom: 175px;
  padding-top: 190px;
}
.hero__inner {
  height: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.hero__title {
  font-family: "nova_bold";
  font-weight: 700;
  font-size: 63px;
  text-transform: uppercase;
  color: #ffffff;
  margin-bottom: 78px;
}
.hero__title span {
  color: #169456;
}
.hero__btn {
  max-width: 305px;
}

.greet {
  background-image: url("../images/bac_greet.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 80px;
  padding-bottom: 100px;
}
.greet__inner {
  display: flex;
  column-gap: 135px;
}
.greet__img {
  position: relative;
  z-index: 1;
}
.greet__img::after {
  content: "";
  position: absolute;
  width: 381px;
  height: 562px;
  left: 0;
  bottom: -61px;
  background: #044d29;
  z-index: -1;
}
.greet__content {
  max-width: 920px;
  padding-top: 100px;
  margin-left: 30px;
  margin-right: 30px;
}
.greet__content-title {
  text-align: left;
}
.greet__content-text {
  margin-top: 30px;
  position: relative;
}
.greet__content-text::after {
  content: "";
  background-color: rgb(88, 208, 149);
  position: absolute;
  width: 2px;
  height: 230px;
  top: 25px;
  left: -25px;
}
.greet__content-text p {
  padding-bottom: 35px;
}
.greet__img {
  width: 470px;
  height: 700px;
}
.greet__content-slogan {
  font-family: "nova_bold";
  font-weight: 700;
  font-size: 21px;
  line-height: 140%;
  color: #044d29;
}

.quality {
  padding-top: 50px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.quality__item {
  text-align: center;
  min-height: 135px;
}
.quality__img {
  width: 118px;
  height: 118px;
  padding-bottom: 6px;
}
.quality__text {
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 21px;
  line-height: 140%;
  color: #044d29;
}

.video {
  background-image: url("../images/bac_video.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.video__inner {
  padding-bottom: 175px;
  padding-top: 120px;
  max-height: 885px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.video__content {
  max-height: 470px;
  max-width: 1020px;
  position: relative;
}
.video__content::after {
  position: absolute;
  content: "";
  background-image: url("../images/video_logo.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 118px;
  height: 118px;
}
.video__content img {
  width: 100%;
  height: 100%;
}
.video__title {
  padding: 10px 10px 70px;
}
.departure {
  background-color: #044d29;
  border-radius: 20px;
  color: #fff;
  padding-top: 95px;
  padding-bottom: 95px;
}
.departure .container-big {
  position: relative;
}
.departure .container-big::after {
  content: "";
  width: 950px;
  height: 754px;
  background-image: url("../images/form_img.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
  right: 0;
  bottom: -166px;
}
.departure__title {
  text-align: left;
  color: #ffffff;
}
.departure__form-content {
  padding-bottom: 75px;
}
.departure__form-content input {
  width: 100%;
  max-width: 410px;
  height: 55px;
  border-radius: 10px;
  padding: 10px;
}
.departure__form-content input + input {
  margin-left: 72px;
}
.departure__form p {
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 141.5%;
  color: #d9d9d9;
  padding: 35px 0 25px;
}
.departure__img {
  max-width: 810px;
  max-height: 710px;
}
.departure__img img {
  width: 100%;
  height: 100%;
}

.steps {
  background-image: url("../images/bac_steps.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 170px;
  padding-bottom: 180px;
}
.steps__inner {
  margin: 30px auto;
  max-width: 785px;
}
.steps__list-num {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: -46px;
  top: 11px;
  width: 92px;
  height: 92px;
  background: #044d29;
  border-radius: 5px;
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 141.5%;
  color: #ffffff;
}
.steps__list-item {
  position: relative;
  display: flex;
  margin-left: 45px;
  align-items: center;
  background: #ffffff;
  box-shadow: 0px 17px 111px rgba(0, 0, 0, 0.11);
  border-radius: 5px;
  padding: 15px 10px 15px 92px;
  margin-bottom: 30px;
  min-height: 114px;
}
.steps__list-title {
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  color: #000000;
}
.steps__list-text {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 141.5%;
  color: rgba(0, 0, 0, 0.6);
}

.calc {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0.7) 0%, rgba(39, 6, 6, 0.7) 100%);
  border-radius: 20px;
  padding-top: 90px;
  padding-bottom: 70px;
  color: #fff;
}
.calc__title {
  color: #fff;
}
.calc__form {
  justify-content: center;
  margin: 78px auto 0;
  display: flex;
  align-items: center;
  flex-direction: column;
}
.calc__form label {
  font-style: normal;
  font-weight: 500;
  font-size: 33px;
  line-height: 40px;
  color: #f8f8f8;
}
.calc__form-inner {
  width: 100%;
  max-width: 580px;
  max-height: 260px;
}
.calc__form-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 35px;
}
.calc__form-area, .calc__form-price {
  background: #ffffff;
  border-radius: 20px;
  max-width: 115px;
  min-height: 65px;
  padding: 5px;
}
.calc__form-price {
  min-width: 115px;
}
.calc__form-phone {
  background: #ffffff;
  border-radius: 20px;
  max-width: 275px;
  min-height: 65px;
  padding: 5px;
}
.calc__form-btn {
  margin-top: 50px;
}

.producers {
  background-image: url("../images/bac_steps.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 180px;
  padding-bottom: 285px;
}
.producers__inner {
  margin-top: 75px;
}
.producers__item {
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 20px;
  padding-bottom: 20px;
}
.producers__item-img {
  background-color: #fff;
  width: 160px;
  height: 135px;
}

.type {
  transform: translateY(-100px);
}
.type__inner {
  margin-top: 35px;
  display: flex;
  justify-content: space-between;
  column-gap: 7px;
}
.type__item {
  position: relative;
  border-radius: 5px;
}
.type__item::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: linear-gradient(0deg, #044d29 7.34%, rgba(4, 77, 41, 0) 100%);
  border-radius: 5px;
}
.type__item-img img {
  width: 100%;
}
.type__item-subtitle {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  text-align: center;
  font-family: "nova_bold";
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 141.5%;
  color: #ffffff;
  z-index: 2;
}

.slider {
  margin-top: 130px;
  margin-bottom: 160px;
  position: relative;
}
.slider::after {
  content: "";
  position: absolute;
  height: 400px;
  width: 100%;
  left: 0;
  top: 56%;
  transform: translateY(-50%);
  background: #bcbcbc;
  border-radius: 5px;
  z-index: -1;
}
.slider__inner {
  margin: 10px auto 50px;
  max-width: 1230px;
  width: 100%;
  height: 560px;
  background: #d9d9d9;
  border-radius: 5px;
}

.services {
  margin-bottom: 215px;
}
.services__title {
  margin-bottom: 35px;
}
.services__inner {
  display: flex;
  justify-content: space-between;
  column-gap: 7px;
  margin-bottom: 7px;
}
.services__item {
  position: relative;
  border-radius: 5px;
  height: 310px;
}
.services__item-img {
  height: 100%;
}
.services__item-img img {
  width: 100%;
  height: 100%;
}
.services__item-subtitle {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  text-align: center;
