}
.footer__number p {
  font-size: 13px;
  line-height: 130%;
  color: #787878;
  position: relative;
  padding-left: 23px;
}
.footer__number p::before {
  content: "";
  position: absolute;
  top: 4px;
  left: 0;
  right: 0;
  bottom: 0;
  width: 7px;
  height: 7px;
  background-color: #044d29;
  border-radius: 50%;
}

.menu {
  position: relative;
}

#menu__toggle {
  display: none;
}

.menu__btn {
  display: none;
}

@media (max-width: 1200px) {
  .menu__btn {
    display: flex;
    position: absolute;
    top: 0px;
    right: 0px;
    width: 26px;
    height: 26px;
    cursor: pointer;
    z-index: 1;
  }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: #044d29;
  }
  .menu__btn > span {
    background-color: #4c1b1b;
  }
  .menu__btn > span::before {
    content: "";
    top: -8px;
  }
  .menu__btn > span::after {
    content: "";
    top: 8px;
  }
  .menu__list {
    display: block;
    position: fixed;
    visibility: hidden;
    top: 0;
    right: -100%;
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 120px 0;
    list-style: none;
    text-align: center;
    background-color: #eceff1;
    box-shadow: 1px 0px 6px rgba(0, 0, 0, 0.2);
  }
  .menu__list-item {
    display: block;
    padding: 12px 24px;
    color: rgba(0, 0, 0, 0.6);
    font-family: nova_regular, sans-serif;
    font-size: 20px;
    font-weight: 600;
    text-decoration: none;
  }
  .menu__list-item:hover {
    background-color: #cfd8dc;
  }
  #menu__toggle:checked ~ .menu__btn > span {
    transform: rotate(45deg);
  }
  #menu__toggle:checked ~ .menu__btn > span::before {
    top: 0;
    transform: rotate(0);
  }
  #menu__toggle:checked ~ .menu__btn > span::after {
    top: 0;
    transform: rotate(90deg);
  }
  #menu__toggle:checked ~ .menu__list {
    visibility: visible;
    right: 0;
  }
  .menu__btn > span,
  .menu__btn > span::before,
  .menu__btn > span::after {
    transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
  }
  .menu__list {
    transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
  }
  .menu__list-item {
    transition: 0.5s cubic-bezier(0.42, 0.61, 0.58, 0.41);
  }
}
