.slider {
  margin-top: 130px;
  margin-bottom: 160px;
  position: relative;
  &::after {
    content: "";
    position: absolute;
    height: 400px;
    width: 100%;
    left: 0;
    top: 56%;
    transform: translateY(-50%);
    background: #bcbcbc;
    border-radius: 5px;
    z-index: -1;
  }
  &__title {
  }
  &__inner {
    margin: 10px auto 50px;
    max-width: 1230px;
    width: 100%;
    height: 560px;
    background: #d9d9d9;
    border-radius: 5px;
  }
}
