.steps {
  background-image: url("../images/bac_steps.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  padding-top: 170px;
  padding-bottom: 180px;
  &__title {
  }
  &__inner {
    margin: 30px auto;
    max-width: 785px;
  }
  &__list {
  }
  &__list-num {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: -46px;
    top: 11px;
    width: 92px;
    height: 92px;
    background: #044d29;
    border-radius: 5px;
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 40px;
    line-height: 141.5%;
    color: #ffffff;
  }
  &__list-content {
  }
  &__list-item {
    position: relative;
    display: flex;
    margin-left: 45px;
    align-items: center;
    background: #ffffff;
    box-shadow: 0px 17px 111px rgba(0, 0, 0, 0.11);
    border-radius: 5px;
    padding: 15px 10px 15px 92px;
    margin-bottom: 30px;
    min-height: 114px;
  }
  &__list-title {
    font-family: "nova_bold";
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    color: #000000;
  }
  &__list-text {
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 141.5%;
    color: #00000099;
  }
}
