.departure {
  & .container-big {
    position: relative;
    &::after {
      content: "";
      width: 950px;
      height: 754px;
      background-image: url("../images/form_img.png");
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center;
      position: absolute;
      right: 0;
      bottom: -166px;
    }
  }
  background-color: #044d29;
  border-radius: 20px;
  color: #fff;
  padding-top: 95px;
  padding-bottom: 95px;

  &__title {
    text-align: left;
    color: #ffffff;
  }
  &__inner {
  }
  &__form {
    &-content {
      padding-bottom: 75px;
      & input {
        width: 100%;
        max-width: 410px;
        height: 55px;
        border-radius: 10px;
        padding: 10px;
        & + input {
          margin-left: 72px;
        }
      }
    }
    & p {
      font-style: normal;
      font-weight: 400;
      font-size: 30px;
      line-height: 141.5%;
      color: #d9d9d9;
      padding: 35px 0 25px;
    }
  }
  &__img {
    max-width: 810px;
    max-height: 710px;
    & img {
      width: 100%;
      height: 100%;
    }
  }
}
