.call {
  background-color: #044d29;
  border-radius: 20px;
  color: #fff;
  text-align: center;
  padding-top: 95px;
  padding-bottom: 95px;
  margin-bottom: 215px;
  &__inner {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__title {
    color: #fff;
  }
  &__subtitle {
    font-size: 30px;
    line-height: 141.5%;
    color: #d9d9d9;
    padding: 20px 0 50px;
  }
  &__content {
  }
  &__form {
  }
  &__form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    & p {
      font-size: 17px;
      line-height: 141.5%;
      color: #d9d9d9;
      padding: 25px 0 35px;
    }
  }
  &__form-content {
    display: flex;
    flex-direction: column;
    align-items: center;
    & input {
      width: 410px;
      height: 55px;
      border-radius: 10px;
      padding: 17px 20px;
      & + input {
        margin-top: 25px;
      }
    }
  }

  &__form-btn {
  }
}
