.calc {
  background: linear-gradient(
    180deg,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(39, 6, 6, 0.7) 100%
  );
  border-radius: 20px;
  padding-top: 90px;
  padding-bottom: 70px;
  color: #fff;
  &__title {
    color: #fff;
  }

  &__form {
    justify-content: center;
    margin: 78px auto 0;
    display: flex;
    align-items: center;
    flex-direction: column;
    & label {
      font-style: normal;
      font-weight: 500;
      font-size: 33px;
      line-height: 40px;
      color: #f8f8f8;
    }
  }
  &__form-inner {
    width: 100%;
    max-width: 580px;
    max-height: 260px;
  }
  &__form-item {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 35px;
  }
  &__form-area,
  &__form-price {
    background: #ffffff;
    border-radius: 20px;
    max-width: 115px;
    min-height: 65px;
    padding: 5px;
  }
  &__form-price {
    min-width: 115px;
  }
  &__form-phone {
    background: #ffffff;
    border-radius: 20px;
    max-width: 275px;
    min-height: 65px;
    padding: 5px;
  }
  &__form-btn {
    margin-top: 50px;
  }
}
